<template>
  <div class="message-list">
    <div class="message-filters">
      <div class="search-container">
        <form>
          <input type="text" placeholder="Search.." name="search" v-model="search">
          <i class="fa fa-search search-span"></i>
        </form>
      </div>
    </div>
    <div class="tableFixHead">
      <table class="table message-list-table">
        <thead>
        <tr>
          <th class="checkbox"><input type="checkbox"/></th>
          <th class="status">Status</th>
          <th class="message">Message</th>
          <th class="indicators">Indicators</th>
          <th class="participants">Participants</th>
          <th class="created-date">Created</th>
          <th class="actions">Actions</th>
        </tr>
        </thead>
        <tbody>
        <MessageListItem
          v-for="message in filteredMesssages"
          :message="message"
          :key="message['.key']"
        />
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import MessageListItem from './MessageListItem'
export default {
  name: 'MessageList',
  components: {
    MessageListItem
  },
  data () {
    return {
      search: ''
    }
  },
  props: {
    messages: {
      required: true,
      type: Array
    }
  },
  computed: {
    filteredMesssages () {
      if (this.search) {
        const filteredMessages = this.messages.filter(message => {
          const props = Object.values(message)
          return props.some(prop => {
            let match = false
            if (prop) {
              if (typeof prop === 'string' &&
                prop.includes(this.search)) {
                match = true
              } else {
                if (prop.toString(10).includes(this.search)) {
                  match = true
                }
              }
            }
            return match
          })
        })
        return filteredMessages
      } else {
        return this.messages
      }
    }
  }
}
</script>

